  padding-left: 1.2rem;
}

.cart-link .counter {
  display: block;
  position: absolute;
  left: 0.2rem;
  top: 0.2rem;
  background-color: #e61f1f;
  width: 1.6rem;
  height: 1.6rem;
  padding: 0.1rem 0 0 0;
