  font-size: 1.2rem;
  line-height: 1.6rem;
  text-align: center;
  color: white;
  border-radius: 0.8rem;
  z-index: 10;
}

.cart-link.push .counter {
  animation: incCount 0.8s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.cart-link.pick .counter {
  animation: decCount 0.8s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

@keyframes incCount {
  0% {
    transform: translate(0, 0.8rem) scale(1.5);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes decCount {
  0% {
    transform: translate(0, -0.8rem) scale(0.8);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

