  position: absolute;
  top: 50%;
  left: 14px;
  width: 18px;
  height: 2px;
  background-color: #6e52dd;
  margin-top: -1px;
  will-change: opacity, transform;
  opacity: 0;
  transition-property: transform, opacity;
  transition-duration: 0.2s;
  transition-delay: 0s;
  transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
