  margin-top: -1px;
  backface-visibility: hidden;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

a.catalog-opener > i::before, a.catalog-opener > i::after {
  content: '';
  display: block;
  position: absolute;
  width: 18px;
  height: 2px;
  background-color: #6e52dd;
  backface-visibility: hidden;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
