  animation: moveToCart 0.5s ease-in-out;
}

@keyframes moveToCart {
  0% {
    opacity: 0.3;
    transform: translate(0, 0) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(0, 0) scale(1.3);
  }
}

.product__title {
  display: block;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #000;
  margin-bottom: 2rem;
}

.product__title span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.product__title:hover {
  color: black;
}

.product__title:hover span {
  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.75);
}

.product__title:last-child {
  margin-bottom: 0;
}

.product__rate {
  display: block;
  width: 90px;
}

.product__rate > span {
  display: block;
  width: 100%;
  height: 14px;
  background: url(../img/rate.jpg) no-repeat 0 0;
}

.product__footer {
  padding: 0 2.4rem;
}

.product__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  min-height: 3.2rem;
}

.product__stock {
  display: flex;
  align-items: center;
  flex-basis: 50%;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #000;
}

.no-flexwrap .product__stock {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product__stock > svg {
  display: inline-block;
  vertical-align: top;
  width: 1.8rem;
  height: 1.8rem;
}

.product__stock > svg path {
  fill: #6e52dd;
}

.product__stock > svg + span {
  padding-left: 0.8rem;
}

.product__stock > span {
  padding-top: 0.2rem;
}

.no-flexwrap .product__stock > span {
  display: inline-block;
  vertical-align: top;
}

.product__stock.off {
  color: #c3c3c3;
}

.product__stock.off > svg path {
  fill: currentColor;
}

.product__price {
  flex-basis: 50%;
  text-align: right;
  line-height: 1;
}

.no-flexwrap .product__price {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product__price.off {
  color: #c3c3c3;
}

.product__price.off > svg path {
  fill: currentColor;
}

.product__price-old,
.product__price-new {
  display: inline-block;
  vertical-align: baseline;
}

.product__price-old {
  line-height: 3.2rem;
  color: #c3c3c3;
}

.product__price-old > strong, .product__price-old > em {
  display: inline-block;
  vertical-align: baseline;
  font-family: "circe-regular", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
}

.product__price-old > strong {
  position: relative;
}

.product__price-old > strong::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 3.8rem;
  height: 0;
  margin: -0.2rem -1.9rem;
  border-top: 1px solid currentColor;
  transform: rotate(-42deg);
}

.product__price-new > strong {
  display: inline-block;
  vertical-align: baseline;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  line-height: 3.2rem;
}

.product__price-new > em {
  display: inline-block;
  vertical-align: baseline;
  font-style: normal;
  font-size: 1.4rem;
  line-height: 1.8rem;
}

.product__option {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1;
  margin: 0 -1.9rem;
  z-index: 1;
}

.product__option::before, .product__option::after {
  content: "";
  display: block;
  border: solid #e6e6e6;
  position: absolute;
  z-index: -1;
}

.product__option::before {
  width: 100%;
  left: 0;
  top: 0;
  border-width: 2px 0 0;
}

.product__option::after {
  width: 0;
  height: 100%;
  left: 50%;
  top: 0;
  border-width: 0 0 0 2px;
  margin-left: -1px;
}

.product__option-btn {
  flex-grow: 1;
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.8rem 1rem;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-decoration: none;
  text-align: center;
  color: #000;
  transition-property: background-color, color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.no-flexwrap .product__option-btn {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product__option-btn > i {
  display: inline-block;
  vertical-align: top;
  width: 2.2rem;
  height: 2.2rem;
}

.product__option-btn > i svg {
  display: inline-block;
  vertical-align: top;
  width: 2.2rem;
  height: 2.2rem;
}

.product__option-btn > i svg path {
  fill: #6e52dd;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.product__option-btn > span {
  padding-top: 0.2rem;
  padding-right: 1rem;
}

.no-flexwrap .product__option-btn > span {
  display: inline-block;
  vertical-align: top;
}

.product__option-btn:first-child > i svg.svg-ico-unlike {
  display: none;
}

.product__option-btn:first-child > i svg path {
  fill: #ef4180;
}

.product__option-btn:first-child.added > i svg.svg-ico-like {
  display: none;
}

.product__option-btn:first-child.added > i svg.svg-ico-unlike {
  display: inline-block;
}

.product__option-btn.added:nth-child(2) > i {
  background-color: #6e52dd;
  border-radius: 0.1rem;
  box-shadow: 0 0 0 6px #6e52dd;
}

.product__option-btn.added:nth-child(2) > i svg path {
  fill: white;
}

.product__option-btn:hover {
  background-color: #6e52dd;
  color: white;
}

.product__option-btn:hover > i svg path {
  fill: white;
}

.product__sale {
  position: absolute;
  top: 0.5rem;
  right: 1.7rem;
  width: 3.4rem;
  height: 3.4rem;
  padding-left: 0.1em;
  border-radius: 1.7rem;
  background-color: #ef4180;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 3.4rem;
  color: white;
  text-align: center;
  pointer-events: none;
}

.product__labels {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.product__label {
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: white;
  text-align: center;
  padding: 1.2rem 2.4rem;
}

.product__label_day {
  background-color: #6e52dd;
}

