}

.article-tiles__label:last-child {
  margin-bottom: 0;
}

.article-tiles__date {
  display: inline-block;
  vertical-align: top;
  font-size: 1.4rem;
  line-height: 1.8rem;
  margin-bottom: 0.5rem;
}

.article-tiles__date:last-child {
  margin-bottom: 0;
}

.article-tiles__item.text-small .article-tiles__title {
