  top: auto;
  bottom: 0;
}

a.article-tiles__inner:hover .article-tiles__title {
  text-decoration: none;
}

/* articles main previews end */
/* helper */
.main-section_helper {
  position: relative;
}

.helper {
  position: relative;
}

.helper__list {
  display: flex;
  margin-bottom: 2rem;
}

.helper__list:last-child {
  margin-bottom: 0;
}

.helper__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  flex-basis: 25%;
  min-height: 12rem;
  text-align: center;
  text-decoration: none;
  color: currentColor;
  background-color: white;
  border: solid #dbdbdb;
  border-width: 0 0 0 1px;
  transition-property: background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.no-flexwrap .helper__item {
  display: inline-block;
  vertical-align: top;
  width: calc(20% - 6px);
}

.touchevents .helper__item {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.helper__item:first-child {
  border: none;
}

.helper__inner {
  width: calc(100% - 4rem);
  display: flex;
  align-items: center;
  text-align: left;
  max-width: 24rem;
}

.helper__inner > svg {
  display: inline-block;
  vertical-align: top;
  width: 3.4rem;
  min-width: 3.4rem;
  height: 3.4rem;
  opacity: 0.8;
}

.helper__inner > svg path {
  fill: #6e52dd;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.helper__inner > svg + span {
  margin-left: 1.5rem;
}

.helper__inner > span {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: rgba(0, 0, 0, 0.8);
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.helper__item:hover {
  background-color: #6e52dd;
  color: white;
}

.helper__item:hover .helper__inner > svg path {
  fill: white;
}

.helper__item:hover .helper__inner > span {
  color: white;
}

/* helper */
/* products */
.products {
  overflow: hidden;
  margin-bottom: 4rem;
}

.products:last-child {
  margin-bottom: 0;
}

.products__list {
  display: flex;
  justify-content: stretch;
  flex-flow: row wrap;
  margin: 0 -1px;
}

.products__item {
  width: 25%;
  flex-basis: 25%;
}

.products__list,
.products__item {
  position: relative;
}

.products__list::before, .products__list:after,
.products__item::before,
.products__item:after {
  content: "";
  display: block;
  position: absolute;
  border: 0 solid #dbdbdb;
  z-index: 10;
  user-select: none;
  pointer-events: none;
  -webkit-tap-highlight-color: transparent;
}

.products__list::before {
  right: 0;
  top: 0;
  border-left-width: 1px;
  height: 9999px;
}

.products__list::after {
  bottom: 0;
  left: 0;
  border-top-width: 1px;
  width: 9999px;
}

.products__item::before {
  left: 0;
  top: 0;
  border-left-width: 1px;
  height: 9999px;
}

.products__item::after {
  left: 0;
  top: 0;
  border-top-width: 1px;
  width: 9999px;
}

[data-view="list-view"] .products__list {
  display: block;
}

[data-view="list-view"] .products__item {
  width: auto;
}

/* products end */
/* m-container */
