}

/* m-container end */
/* view-switcher */
.view-switcher {
  position: relative;
  width: 6.6rem;
}

.view-switcher::after {
  content: '';
  display: table;
  clear: both;
}

.view-switcher::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 2.8rem;
  height: 2.8rem;
  background-color: #6e52dd;
  border-radius: 3px;
  will-change: transform;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.view-switcher[data-view="grid-view"]::before {
  transform: translate(0, 0);
  transform: translate3d(0, 0, 0);
}

.view-switcher[data-view="list-view"]::before {
  transform: translate(3.8rem, 0);
  transform: translate3d(3.8rem, 0, 0);
}

.view-switcher a {
  position: relative;
  float: left;
  display: block;
  width: 2.8rem;
  height: 2.8rem;
  text-decoration: none;
  color: #b0a1f0;
  margin-left: 1rem;
  border-radius: 3px;
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.view-switcher a:first-child {
  margin-left: 0;
}

.view-switcher a svg {
  display: inline-block;
  vertical-align: top;
  width: 2.8rem;
  height: 2.8rem;
}

.view-switcher a path {
  fill: currentColor;
}

.view-switcher a > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.view-switcher a > span:active, .view-switcher a > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.no-touchevents .view-switcher a:not(.active):hover, .no-touchevents .view-switcher a:not(.active):focus {
  color: #6e52dd;
}

.view-switcher a.active {
  cursor: default;
}

/* view-switcher end */
/* options panel */
.filters-switcher_mob {
  display: none;
  position: relative;
  color: currentColor;
  user-select: none;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 20px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: 1px solid #dbdbdb;
  padding: 9px 20px 9px 40px;
  background-color: #fff;
}

@media (max-width: 639px) {
  .filters-switcher_mob {
    display: block;
    padding-top: 14px;
    padding-bottom: 14px;
    border-color: transparent;
  }
}

.filters-switcher_mob .p-filters-activated {
  position: absolute;
  right: 5px;
  top: 5px;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 10px;
  line-height: 1;
  letter-spacing: normal;
  color: #6e52dd;
