}

@media (min-width: 640px) {
  .no-touchevents .filters-switcher_mob:hover {
    border-color: #999999;
  }
}

/* options panel end */
/* p-sorting */
.p-sorting {
  list-style: none;
  margin: 0;
}

.p-sorting:last-child {
  margin-bottom: 0;
}

.p-sorting a {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 2px 10px 2px 25px;
  margin-right: 10px;
  color: #777;
  font-size: 1.4rem;
  line-height: 2.0rem;
  text-decoration: none;
  border-radius: 4px;
}

.p-sorting a span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.p-sorting a:hover {
  color: #777777;
}

.p-sorting a:hover span {
  box-shadow: inset 0 -2px 0 rgba(119, 119, 119, 0.75);
}

.p-sorting a > em {
  position: absolute;
  left: 3px;
  top: 50%;
  width: 2rem;
  height: 2rem;
  margin: -1rem 0;
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  z-index: 5;
  border-radius: 0.4rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  will-change: transform;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.p-sorting a > em::before, .p-sorting a > em::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  height: 0.2rem;
  width: 0.7rem;
  background-color: #6e52dd;
  pointer-events: none;
  margin: -0.1rem -0.5rem;
  transition-property: background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.p-sorting a > em::before {
  transform: rotate(45deg);
  left: 50%;
}

.p-sorting a > em::after {
  transform: rotate(-45deg);
  right: 50%;
}

.p-sorting a:active {
  top: 1px;
}

.p-sorting a.active {
  background-color: #6e52dd;
  color: white;
}

.p-sorting a.active span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.p-sorting a.active:hover {
  color: white;
}

.p-sorting a.active:hover span {
  box-shadow: inset 0 -2px 0 rgba(255, 255, 255, 0.75);
}

.p-sorting a.active > em::before, .p-sorting a.active > em::after {
  background-color: white;
}

.p-sorting a.order-top > em {
  transform: rotate(-0.5turn);
}

/* p-sorting end */
/* filters tags */
.p-filters-tags {
  padding: 0 0 2rem;
  position: relative;
  line-height: 2.0rem;
  margin-left: -1rem;
  display: none;
  -webkit-tap-highlight-color: transparent;
}

.filters-results-show .p-filters-tags {
  display: block;
}

.p-filters-tags__item {
  display: inline-block;
  vertical-align: top;
  position: relative;
  font-size: 1.4rem;
  line-height: 2rem;
  color: #6e52dd;
  white-space: nowrap;
