  margin-left: 1rem;
}

.p-filters-tags__item > span {
  margin-right: 13px;
  white-space: normal;
  text-decoration: underline;
}

.p-filters-tags__item > i {
  position: relative;
  display: inline-block;
  vertical-align: top;
  opacity: 0;
  width: 10px;
  height: 10px;
  margin-left: -10px;
  top: 6px;
  font-size: 0;
