  transition-timing-function: ease-in-out;
}

label.checkbox_value > span::before, label.checkbox_value > span::after {
  content: none;
}

label.checkbox_value input[type="checkbox"]:checked ~ span {
  background-color: #6e52dd;
  border-color: #6e52dd;
  color: white;
}

.no-touchevents label.checkbox_value:hover > span {
  border-color: #6e52dd;
  box-shadow: inset 0 0 0 1px #6e52dd;
}

.no-touchevents .p-filters-select:hover .p-filters-title {
  color: #6e52dd;
}

.p-filters-item.p-filters-on .p-filters-select .p-filters-title {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  color: #6e52dd;
}

.p-filters-item.p-filters-is-open .p-filters-angle {
  transform: rotate(0.5turn);
}

.p-filters-item.p-filters-is-open .p-filters-drop {
  display: block;
}

/* filters end */
/* filters-results */
.p-filters-results {
  position: absolute;
  bottom: 0;
  height: 50px;
  width: 342px;
  background-color: #1a1a1a;
  color: white;
  display: flex;
  justify-content: space-between;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
  transition-delay: 0.5s;
}

@media (max-width: 639px) {
  .p-filters-results {
    width: 100%;
  }
}

.filters-results-show .p-filters-results {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transition-delay: 0s;
}

.filters-result-fixed .p-filters-results {
  height: 75px;
  padding-bottom: 25px;
  position: fixed;
  bottom: -1px;
}

@media (max-width: 991px) {
  .p-filters-results, .filters-result-fixed .p-filters-results {
    z-index: 10100;
    position: fixed;
    left: 0;
    bottom: 0;
  }
}

.p-filters-results .results-loading {
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  position: absolute;
  left: -1px;
  top: -1px;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 50px;
  color: white;
  text-align: center;
  z-index: 5;
  background-color: #999999;
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
}

.js-loading .p-filters-results .results-loading {
  opacity: 1;
  visibility: visible;
}

.p-filters-results__selected {
  padding-right: 10px;
  padding-left: 10px;
  font-size: 0;
  letter-spacing: -4px;
  line-height: 1;
  text-align: center;
  flex: 1 1;
}

.p-filters-results__selected::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.p-filters-results__selected__align {
  display: inline-block;
  vertical-align: middle;
  max-width: calc(100% - 2px);
  text-align: left;
}

.p-filters-results__selected__label {
  font-family: "circe-light", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.2rem;
  line-height: 1.33;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.p-filters-results__selected__count {
  font-family: Georgia, serif;
  font-weight: bold;
  font-size: 1.4rem;
  line-height: 1.14;
  letter-spacing: normal;
}

.p-filters-results__selected__count .cur {
  color: #6e52dd;
}

.p-filters-results__apply {
  display: inline-block;
  vertical-align: top;
  padding: 5px 2px 5px 0;
}

.p-filters-results__apply .btn-filters-apply {
  display: inline-block;
  vertical-align: top;
  height: 100%;
  padding: 0 10px;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 0;
  letter-spacing: -4px;
  line-height: 1;
  color: white;
  text-transform: uppercase;
  background-color: #1a1a1a;
  border: 2px solid white;
  z-index: 1;
  flex: 1 1;
}

.p-filters-results__apply .btn-filters-apply::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.p-filters-results__apply .btn-filters-apply span {
  display: inline-block;
  vertical-align: middle;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 0.05em;
  max-width: calc(100% - 2px);
}

.no-touchevents .p-filters-results__apply .btn-filters-apply:hover {
  color: #1a1a1a;
  background-color: white;
}

.btn-clear-filters {
  width: 52px;
  height: 52px;
  padding: 0;
  background-color: #1a1a1a;
  border: 2px solid #1a1a1a;
  color: white;
}

.btn-clear-filters svg {
  width: 36px;
  height: 36px;
}

.btn-clear-filters path {
  fill: currentColor;
}

.no-touchevents .btn-clear-filters:hover {
  background-color: white;
  border-color: #000;
  color: #000;
}

.btn-clear-filters:disabled {
  background-color: #1a1a1a !important;
  border-color: #1a1a1a !important;
  color: white !important;
}

.loader-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
}

@media (max-width: 991px) {
  .loader-cover {
    position: fixed;
    z-index: 10100;
  }
}

.js-loading .loader-cover {
  opacity: 1;
  visibility: visible;
}

.loader-cover::before {
  content: "";
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  width: 120px;
  height: 120px;
  transform: translate(-50%, -50%);
  background-image: url(../img/preloader.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

/* filters-results end */
/* toggle filters for devices */
@media (max-width: 991px) {
  .m-aside, .p-filters-results {
    will-change: transform;
    transform: translate(-100%, 0);
    transform: translateX(-100%);
    transition-property: transform;
    transition-duration: 0.2s;
  }
  .m-aside.active, .p-filters-results.active {
    transform: translate(0, 0);
    transform: translateX(0);
  }
}

/* toggle filters for devices end */
/* collections */
.collections {
  position: relative;
  padding: 3rem 4rem;
  display: flex;
  align-items: center;
  background-color: white;
}

.collections::before {
  content: "";
  display: block;
  position: absolute;
  top: 3rem;
  bottom: 3rem;
  left: 18rem;
  width: 0;
  border-left: 1px solid #dbdbdb;
}

.collections__title {
  width: 14rem;
  flex-basis: 14rem;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  color: #000;
  font-size: 2.1rem;
  line-height: 2.2rem;
  margin: 0;
}

.collections__content {
  flex-grow: 1;
  flex-basis: calc(100% - 18rem);
  padding: 0 3.5rem;
}

.no-flexwrap .collections__content {
  margin-left: 14rem;
}

.collections__thumbs {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.4rem;
  line-height: 2.0rem;
  margin-bottom: 1rem;
}

.collections__thumbs:last-child {
  margin-bottom: 0;
}

.collections__thumbs a {
  color: #777;
  text-decoration: none;
  margin: 0.5rem 4rem 0.5rem 0;
}

.no-flexwrap .collections__thumbs a {
  display: inline-block;
  vertical-align: top;
}

.collections__thumbs a.touchevents {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.collections__thumbs a span {
  border-bottom: 2px dotted rgba(119, 119, 119, 0.35);
  transition-property: border-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.collections__thumbs a:hover {
  color: #777777;
}

.collections__thumbs a:hover span {
  border-color: #777777;
}

.collections__thumbs a.tabs-active {
  color: #000;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
}

.collections__thumbs a.tabs-active > span {
  border-color: transparent;
}

/* collections end */
/* float-list */
.float-list-wrap {
  overflow: hidden;
}

.float-list {
  font-size: 1.4rem;
  line-height: 2.0rem;
  cursor: default;
}

.float-list, .user-content .float-list {
  list-style: none;
  margin: 0 0 2rem;
  padding: 0;
}

.float-list:last-child, .user-content .float-list:last-child {
  margin-bottom: 0;
}

.float-list li, .user-content .float-list li {
  margin: 1.2rem 2rem 0 0;
  padding: 0;
}

.float-list li:before, .user-content .float-list li:before {
  content: none;
}

.float-list li {
  display: inline-block;
  vertical-align: top;
  color: #6e52dd;
}

.float-list a {
  color: #777;
  text-decoration: none;
  cursor: pointer;
}

.float-list a span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.float-list a:hover {
  color: #777777;
}

.float-list a:hover span {
  box-shadow: inset 0 -2px 0 rgba(119, 119, 119, 0.75);
}

.float-list li.current a {
  color: #6e52dd;
}

.float-list li.current a span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.float-list li.current a:hover {
  color: #6e52dd;
}

.float-list li.current a:hover span {
  box-shadow: inset 0 -2px 0 rgba(110, 82, 221, 0.75);
}

/* float-list end */
/*======================================================*/
/* PRINT VERSION */
/*======================================================*/
@media print {
  *,
  *::before,
  *::after {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a, a:visited {
    text-decoration: underline;
  }
  a[href^=http]::after {
    content: " <" attr(href) "> ";
  }
  a[href]::after {
    content: " (" attr(href) ")";
  }
  abbr[title]::after {
    content: " (" attr(title) ")";
  }
  a[href^="#"]::after, a[href^="javascript:"]::after {
    content: "";
  }
  pre, blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr, img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
  hr {
    border-color: black;
  }
  body {
    font-family: Arial, Helvetica, sans-serif !important;
    font-size: 10pt !important;
    line-height: 1.5 !important;
    min-width: 1024px !important;
  }
  h1, h2, h3, h4, h5, h6,
  b, strong, th, i, em,
  blockquote, q, cite,
  table td, table th, table {
    font-family: Arial, Helvetica, sans-serif !important;
    font-weight: normal !important;
    font-style: normal !important;
  }
  h1 {
    font-size: 20pt !important;
    line-height: 1.3 !important;
  }
  h2 {
    font-size: 18pt !important;
    line-height: 1.4 !important;
  }
  h3, .main-slider-title__item {
    font-size: 16pt !important;
    line-height: 1.5 !important;
  }
  h4, blockquote q {
    font-size: 14pt !important;
    line-height: 1.5 !important;
  }
  h5, blockquote cite {
    font-size: 12pt !important;
    line-height: 1.6 !important;
  }
  h6 {
    font-size: 8pt !important;
    line-height: 1.8 !important;
  }
  b, strong, th {
    font-weight: bold !important;
  }
  i, em {
    font-style: italic !important;
  }
  i b, i strong, em b, em strong,
  b i, b em, strong i, strong em {
    font-weight: bold !important;
    font-style: italic !important;
  }
  table td, table th, table {
    border: 1px solid black !important;
    font-size: 9pt !important;
    color: black !important;
    padding: 3pt !important;
  }
  table td > *, table th > *, table > * {
    padding: 0 !important;
  }
  .sidebar, .aside, .footer, .spacer,
  .print-hidden, .view-print,
  .c-nav, .nav-list,
  .breadcrumbs, .breadcrumbs__list,
  [class*="btn-"],
  form,
  .flashes,
  .images-slider__item::before,
  .images-slider__list .slick-arrow,
  .images-slider__list .slick-dots,
  .images-slider__list .slider-counter,
  .images-slider__item img,
  .class {
    display: none !important;
  }
  .images-slider__list,
  .images-slider__list .slick-list,
  .images-slider__list .slick-track,
  .images-slider__item,
  .images-slider__item .caption,
  .class {
    display: block !important;
    float: none !important;
    position: relative !important;
    overflow: visible !important;
    height: auto !important;
    min-height: 0 !important;
    max-height: none !important;
    width: auto !important;
    min-width: 0 !important;
    max-width: none !important;
    left: auto !important;
    top: auto !important;
    right: auto !important;
    bottom: auto !important;
    padding: 0 !important;
    margin: 0 !important;
    transform: none !important;
    text-align: left !important;
    border: none !important;
    box-shadow: none !important;
    opacity: 1 !important;
    visibility: visible !important;
    z-index: auto !important;
  }
  .images-slider__item,
  .class {
    margin-top: 0 !important;
    margin-bottom: 0.5cm !important;
    padding-bottom: 0 !important;
  }
  .header, .blockquote, hr, blockquote,
  .class {
    margin-top: 0 !important;
    margin-bottom: 1cm !important;
    padding-bottom: 0 !important;
  }
  .logo {
    background: none !important;
  }
  .logo > * {
    display: none !important;
  }
  .logo > img {
    display: block !important;
    float: left !important;
  }
  blockquote q::before,
  blockquote q::after {
    padding: 0 !important;
    margin: 0 !important;
    font-size: 18pt !important;
    display: inline-block !important;
  }
  table {
    width: 100% !important;
  }
  .user-content img {
    max-width: 8cm !important;
  }
}
