/* m-container end */

/* view-switcher */
.view-switcher {
  $size: 2.8rem;
  $space: 1rem;

  position: relative;
  width: $size * 2 + $space;
  @include clear;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: $size;
    height: $size;
    background-color: $base-color;
    border-radius: 3px;
    // transition
    $prop: transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } }

  &[data-view="grid-view"] {
    &::before {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  &[data-view="list-view"] {
    &::before {
      $translate: $size + $space, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  a {
    position: relative;
    float: left;
    display: block;
    width: $size;
    height: $size;
    text-decoration: none;
    color: #b0a1f0;
    margin-left: $space;
    border-radius: 3px;
    // transition
    $prop: color;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    &:first-child {
      margin-left: 0; }

    svg {
      display: inline-block;
      vertical-align: top;
      width: $size;
      height: $size; }

    path {
      fill: currentColor; }

    & > span {
      @include hide-text; }

    // hover
    .no-touchevents &:not(.active) {
      &:hover, &:focus {
        color: $base-color; } }

    // active
    &.active {
      cursor: default; } } }

/* view-switcher end */

/* options panel */
.filters-switcher_mob {
  display: none;
  position: relative;
  color: currentColor;
  user-select: none;
  @include font-bold;
  @include font(1.4rem, 20px);
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: 1px solid $border-color;
  padding: 9px 20px 9px 40px;
  background-color: #fff;

  @include media('<mob-lg') {
    display: block;
    @include padding-vertical(14px);
    border-color: transparent; }

  .p-filters-activated {
    position: absolute;
    right: 5px;
    top: 5px;
    @include font-bold;
    font-size: 10px;
    line-height: 1;
    letter-spacing: normal;
    color: $base-color;
    z-index: 2;

    &.hide {
      display: none; } }

  svg {
    display: block;
    position: absolute;
    left: 12px;
    top: 50%;
    @include size(18px);
    margin-top: -9px; }

  path {
    fill: currentColor; }

  // hover
  .no-touchevents &:hover {
    @include media('>=mob-lg') {
      border-color: $form-border-color--focus; } } }

/* options panel end */

/* p-sorting */
.p-sorting {
  $angle-size: 2.0rem;
  $arrow-size: 0.7rem;

  list-style: none;
  margin: 0;

  &:last-child {
    margin-bottom: 0; }

  a {
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding: 2px 10px 2px 25px;
    margin-right: 10px;
    color: #777;
    font-size: 1.4rem;
    line-height: 2.0rem;
    text-decoration: none;
    border-radius: 4px;

    @include underlinedLinks(#777);

    & > em {
      position: absolute;
      left: 3px;
      top: 50%;
      width: $angle-size;
      height: $angle-size;
      margin: -#{round-float($angle-size / 2, 1)} 0;
      text-indent: -9999px;
      overflow: hidden;
      cursor: pointer;
      z-index: 5;
      border-radius: 0.4rem;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      // transition
      $prop: transform;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; }

      &::before, &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        height: 0.2rem;
        width: $arrow-size;
        background-color: $base-color;
        pointer-events: none;
        margin: -0.1rem -0.5rem;
        // transition
        $prop: background-color;
        transition: {
          property: $prop;
          duration: $animation-speed;
          timing-function: ease-in-out; } }

      &::before {
        transform: rotate(45deg);
        left: 50%; }

      &::after {
        transform: rotate(-45deg);
        right: 50%; } }

    // click
    &:active {
      top: 1px; }

    // active
    &.active {
      $color: white;
      background-color: $base-color;
      color: $color;

      @include underlinedLinks($color);

      & > em {
        &::before, &::after {
          background-color: $color; } } }

    // order-top
    &.order-top > em {
      $rotate: -0.5turn;
      transform: rotate($rotate); } } }

/* p-sorting end */

/* filters tags */
.p-filters-tags {
  //position: sticky
  //top: 0
  padding: 0 0 2rem;
  position: relative;
  line-height: 2.0rem;
  margin-left: -1rem;
  display: none;
  -webkit-tap-highlight-color: transparent;

  .filters-results-show & {
    display: block; } }

.p-filters-tags__item {
  $this-icon-lines-width: 10px;
  $this-icon-lines-height: 1px;
  //display: inline
  display: inline-block;
  vertical-align: top;
  position: relative;
  @include font(1.4rem, 2.0rem);
  color: $base-color;
  white-space: nowrap;
  cursor: pointer;
  margin-left: 1rem;

  & > span {
    margin-right: $this-icon-lines-width + 3;
    white-space: normal;
    text-decoration: underline; }

  & > i {
    position: relative;
    display: inline-block;
    vertical-align: top;
    opacity: 0;
    @include size(#{round($this-icon-lines-width)});
    margin-left: -$this-icon-lines-width;
    top: 6px;
    @include font(0, 0);
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    &::before, &::after {
      @include pseudo();
      left: 50%;
      top: 50%;
      backface-visibility: hidden;
      @include size($this-icon-lines-width, $this-icon-lines-height);
      margin: -#{round($this-icon-lines-height*0.5)} 0 0 -#{round($this-icon-lines-width*0.5)};
      background-color: currentColor; }

    &::before {
      transform: rotate(-45deg); }

    &::after {
      transform: rotate(45deg); } }

  .no-touchevents &:hover {
    //& > span
    //text-decoration: none

    & > i {
      opacity: 1; } } }

/* filters tags end */

/* btn-filters-close */
.btn-filters-close {
  display: block;
  position: relative;
  background-color: $bg-color;
  @include font-bold;
  @include font(14px, $header-height--sm);
  letter-spacing: 0.1em;
  text-align: left;
  color: #fff;
  text-transform: uppercase;
  padding: 0 nth($p-filters-space, 2);
  border: 1px solid #666;
  z-index: 5;

  @include media('<mob-lg') {
    @include padding-horizontal(15px); }

  & > div {
    position: relative;
    display: inline-block; }

  .p-filters-activated {
    @include font-bold;
    color: $base-color;
    @include font(10px, 1);
    position: absolute;
    top: 50%;
    left: 100%;
    margin: -10px 0 0 5px; }

  .svg-ico-close {
    display: block;
    @include size(20px);
    position: absolute;
    right: 15px;
    top: 50%;
    margin-top: -10px;

    path {
      fill: currentColor; } } }

/* btn-filters-close end */

/* filters */
.p-filters-list {
  position: relative;
  padding: 0 2rem; }

.p-filters-item {
  position: relative;
  border: solid $border-color;
  border-width: 0 0 1px; }

// switcher
.p-filters-select {
  position: relative;
  cursor: pointer;
  padding: nth($p-filters-space, 1) nth($p-filters-space, 2) nth($p-filters-space, 1) 3.3rem;
  line-height: 1;
  user-select: none;
  -webkit-tap-highlight-color: transparent; }

// title
.p-filters-title {
  @include font-normal;
  @include font(1.8rem, 2.6rem);
  color: #000; }

// angle
.p-filters-angle {
  position: absolute;
  left: nth($p-filters-space, 2);
  top: 50%;
  @include size($p-filters-angle-size);
  margin-top: -#{round-float($p-filters-angle-size / 2, 1)};
  border: 1px solid $border-color;
  border-radius: round-float($p-filters-angle-size / 2, 1);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  & > i {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: $p-filters-angle-arrow-size;
    width: $p-filters-angle-arrow-size;
    margin: {}
    border: solid $base-color;
    border-width: 0 0 0.2rem 0.2rem;
    margin: -0.5rem -0.4rem;
    transform: rotate(-45deg); } }

// counter
.p-filters-selected {
  position: absolute;
  right: 5px;
  top: 3px;
  z-index: 2;
  @include font(1.4rem, round-float(18/14, 2));
  opacity: 0;
  pointer-events: none;
  //===================
  $param: transform, opacity;
  will-change: $param;
  transition: {
    property: $param; }

  &.filters-selected-show {
    opacity: 1;
    transition: {
      duration: $animation-speed; } } }


// drop
.p-filters-drop {
  position: relative;
  display: none; }

.p-filters-drop-list {
  $item-space: 1.4rem;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 33rem;

  &, .#{$user-content} & {
    list-style: none;
    margin: 0;
    padding: 0 nth($p-filters-space, 2) nth($p-filters-space, 1) - $item-space;

    li {
      position: relative;
      padding-left: 0;
      margin: 0 0 $item-space;

      &::before {
        content: none; }

      &:first-child {
        &:before {
          content: none; } } } }

  label {
    display: block; }

  // item float
  &.item-float {
    @include margin-horizontal(-0.6rem);
    @include clear;

    li {
      float: left;
      margin-left: 0.6rem;
      line-height: 1; } }

  // group of form's elements
  .form-row {
    $space: 2rem;
    margin-left: -#{round-float($space / 2, 1)};
    margin-right: -#{round-float($space / 2, 1)};

    .input-wrap {
      margin-left: round-float($space / 2, 1);
      margin-right: round-float($space / 2, 1);
      width: calc(100% / 2 - #{$space}); } } }

label.checkbox_value {
  & > span {
    display: inline-block;
    vertical-align: top;
    height: 3.2rem;
    border-radius: 1.6rem;
    text-align: center;
    padding: 0.5rem 1rem;
    font-size: 1.4rem;
    line-height: 2.0rem;
    border: 1px solid $border-color;
    background-color: white;
    // transition
    $prop: color, background-color, border-color, box-shadow;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    &::before, &::after {
      content: none; } }

  input[type="checkbox"]:checked ~ span {
    background-color: $base-color;
    border-color: $base-color;
    color: white; }

  .no-touchevents &:hover > span {
    border-color: $base-color;
    box-shadow: inset 0 0 0 1px $base-color; } }

// ===STATES===
// hover
.p-filters-select {
  .no-touchevents &:hover {
    .p-filters-title {
      color: $base-color; } } }

// has checked filters
.p-filters-item.p-filters-on {
  .p-filters-select {
    .p-filters-title {
      @include font-bold;
      color: $base-color; } } }

// drop open
.p-filters-item.p-filters-is-open {

  .p-filters-angle {
    transform: rotate(0.5turn); }

  .p-filters-drop {
    display: block; } }

/* filters end */

/* filters-results */
.p-filters-results {
  position: absolute;
  bottom: 0;
  height: 50px;
  width: $p-filters-width;
  background-color: $header-bg;
  //border: solid $header-bg
  //background-color: #eee
  //border: solid $border-color
  //border-width: 1px 0
  color: white;
  display: flex;
  justify-content: space-between;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed;
    delay: 0.5s; }

  @include media('<mob-lg') {
    width: 100%; }

  .filters-results-show & {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transition: {
      delay: 0s; } }

  .filters-result-fixed & {
    height: 75px;
    padding-bottom: 25px;
    position: fixed;
    bottom: -1px; }

  &, .filters-result-fixed & {
    @include media('<tab-lg') {
      z-index: 10100;
      position: fixed;
      left: 0;
      bottom: 0; } }
  //overflow: hidden

  .results-loading {
    @include size(calc(100% + 2px));
    position: absolute;
    left: -1px;
    top: -1px;
    @include font-bold;
    @include font(1.6rem, 50px);
    color: white;
    text-align: center;
    z-index: 5;
    background-color: rgba(153, 153, 153, 1);
    opacity: 0;
    visibility: hidden;
    transition: {
      property: opacity, visibility;
      duration: $animation-speed; }

    .js-loading & {
      opacity: 1;
      visibility: visible; } } }

// selected
.p-filters-results__selected {
  @include padding-horizontal(10px);
  @include clear-spacing;
  line-height: 1;
  text-align: center;

  flex: 1 1;

  &::before {
    content: "";
    @include display-inline-block-middle;
    height: 100%; } }

.p-filters-results__selected__align {
  @include display-inline-block-middle;
  max-width: calc(100% - 2px);
  text-align: left; }

.p-filters-results__selected__label {
  @include font-light;
  @include font(1.2rem, round-float(16/12, 2));
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.p-filters-results__selected__count {
  @include font-alt-bold;
  @include font(1.4rem, round-float(16/14, 2));
  letter-spacing: normal;

  .cur {
    color: $base-color; } }

// apply btn
.p-filters-results__apply {
  @include display-inline-block-top;
  padding: 5px 2px 5px 0;

  .btn-filters-apply {
    @include display-inline-block-top;
    //height: calc(100% + 2px)
    height: 100%;
    //margin: -1px 0
    padding: 0 10px;
    @include font-bold;
    @include clear-spacing;
    line-height: 1;
    color: white;
    text-transform: uppercase;
    background-color: $header-bg;
    border: 2px solid white;
    z-index: 1;

    flex: 1 1;

    &::before {
      content: "";
      @include display-inline-block-middle;
      height: 100%; }

    span {
      @include display-inline-block-middle;
      @include font(13px, 20px);
      letter-spacing: 0.05em;
      max-width: calc(100% - 2px); }

    // hover
    .no-touchevents &:hover {
      color: $header-bg;
      background-color: white; } } }
//background-color: tint($btn-bg-hover-color, 80)

// btn-clear-filters
.btn-clear-filters {
  @include size(52px);
  padding: 0;
  background-color: $header-bg;
  border: 2px solid $header-bg;
  color: white;

  svg {
    @include size(36px); }

  path {
    fill: currentColor; }

  // hover
  .no-touchevents &:hover {
    background-color: white;
    border-color: $btn-bg;
    color: $btn-bg; }

  // focus
  &:disabled {
    background-color: $header-bg !important;
    border-color: $header-bg !important;
    color: white !important; } }

.loader-cover {
  @include size(100%);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.50);
  opacity: 0;
  visibility: hidden;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed; }

  @include media('<tab-lg') {
    position: fixed;
    z-index: 10100; }

  .js-loading & {
    opacity: 1;
    visibility: visible; }

  &::before {
    $loader-size: 120px;
    // ==================
    content: "";
    display: block;
    position: fixed;
    left: 50%;
    top: 50%;
    @include size($loader-size);
    transform: translate(-50%, -50%);
    background-image: url(../../img/preloader.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain; } }

/* filters-results end */

/* toggle filters for devices */
.m-aside, .p-filters-results {
  @include media('<tab-lg') {
    //opacity: 0
    //visibility: hidden
    will-change: transform;
    transform: translate(-100%, 0);
    transform: translateX(-100%);
    transition: {
      property: transform;
      duration: $animation-speed; }

    &.active {
      transform: translate(0, 0);
      transform: translateX(0); } } }

/* toggle filters for devices end */

/* collections */
$collections-space: 3.0rem 4.0rem;
$collections-title-width: 14rem;
.collections {
  position: relative;
  padding: $collections-space;
  display: flex;
  align-items: center;
  background-color: white;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: nth($collections-space, 1);
    bottom: nth($collections-space, 1);
    left: $collections-title-width + nth($collections-space, 2);
    width: 0;
    border-left: 1px solid $border-color; } }

.collections__title {
  width: $collections-title-width;
  flex-basis: $collections-title-width;
  @include font-bold;
  color: #000;
  font-size: 2.1rem;
  line-height: 2.2rem;
  margin: 0; }

.collections__content {
  flex-grow: 1;
  flex-basis: calc(100% - #{$collections-title-width + nth($collections-space, 2)});
  padding: 0 3.5rem;

  // for ie 9
  .no-flexwrap & {
    margin-left: $collections-title-width; } }

.collections__thumbs {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.4rem;
  line-height: 2.0rem;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; }

  a {
    color: #777;
    text-decoration: none;
    margin: 0.5rem 4rem 0.5rem 0;

    // for ie 9
    .no-flexwrap & {
      display: inline-block;
      vertical-align: top; }

    // touch
    &.touchevents {
      user-select: none;
      -webkit-tap-highlight-color: transparent; }

    @include underlinedAnchor(#777);

    // active
    &.tabs-active {
      color: #000;
      @include font-bold;

      & > span {
        border-color: transparent; } } } }

/* collections end */

/* float-list */
.float-list-wrap {
  overflow: hidden; }

.float-list {
  font-size: 1.4rem;
  line-height: 2.0rem;
  cursor: default;

  &, .#{$user-content} & {
    list-style: none;
    margin: 0 0 2rem;
    padding: 0;

    &:last-child {
      margin-bottom: 0; }

    li {
      margin: 1.2rem 2rem 0 0;
      padding: 0;

      &:before {
        content: none; } } }

  li {
    display: inline-block;
    vertical-align: top;
    color: $base-color; }

  a {
    color: #777;
    text-decoration: none;
    cursor: pointer;

    @include underlinedLinks(#777); }

  li.current {
    a {
      color: $base-color;

      @include underlinedLinks($base-color); } } }

/* float-list end */
