@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* swiper slider */
.swiper-container, .swiper-pagination, .slider-arrows {
  opacity: 0;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .is-loaded &, &.is-loaded, .no-js & {
    opacity: 1; } }

.swiper-container, .swiper-pagination-bullet, .slider-arrows {
  pointer-events: none;

  .is-loaded &, &.is-loaded, .no-js & {
    pointer-events: auto; } }

// slider arrows
.slider-arrows {
  height: 0;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 100%;
  //margin-bottom: $bottom-space--article
  pointer-events: none;

  @include media('<desk-sm');
  //margin-bottom: $bottom-space--article--md

  @include media('<mob-lg') {
    //margin-bottom: $bottom-space--article--mob
    display: none; } }

.slider-arrows__holder {
  position: relative; }

.slider-arrow {
  position: absolute;
  top: 50%;
  @include size(nth($slider-button-size, 2), nth($slider-button-size, 1));
  margin-top: -#{round(nth($slider-button-size, 2)/2)};
  color: $base-color;
  will-change: transform;
  //background: rgba(91, 255, 176, 0.51)
  // для тестирования, чтобы видеть контуры кнопки
  //background: none
  z-index: 5;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  .slider-arrows & {
    left: auto;
    top: auto;
    bottom: 0;
    pointer-events: auto;
    margin: round((50 - nth($slider-button-size, 1))/2) 0;

    @include media('<desk-sm') {
      margin: round((38 - nth($slider-button-size, 1))/2) 0; }

    @include media('<mob-sm') {
      margin: round((30 - nth($slider-button-size, 1))/2) 0; } }

  svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: $slider-arrow-size;
    height: $slider-arrow-size;
    margin: -#{round($slider-arrow-size/2)} 0 0 -#{round($slider-arrow-size/2)};
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    @include media('<desk-sm') {
      width: $slider-arrow-size--mob;
      height: $slider-arrow-size--mob;
      margin: -#{round($slider-arrow-size--mob/2)} 0 0 -#{round($slider-arrow-size--mob/2)}; }


    & + svg {
      opacity: 0; } }

  path {
    fill: currentColor; } }

// prev arrow
.slider-arrow_prev {
  left: 0;

  .slider-arrows & {
    right: nth($slider-button-size, 2); } }

// next arrow
.slider-arrow_next {
  right: 0;

  .slider-arrows & {
    right: 0; } }

// hover, disabled, active
.slider-arrow {

  // hover
  &:not(.swiper-button-disabled):hover {
    svg {
      & + svg {
        opacity: 1; } } }

  // active
  &:not(.swiper-button-disabled):active {
    svg {
      transform: translate(0, 1px); } }

  // disabled
  &.swiper-button-disabled {
    pointer-events: auto;
    cursor: default;
    opacity: 0.33; } }


/* slider pagination */
.swiper-pagination {
  pointer-events: none; }

.swiper-pagination-bullet {
  display: inline-block;
  vertical-align: top;
  $size: 8px;
  @include size($size);
  border-radius: round($size / 2);
  border: none;
  color: $base-color;
  opacity: 0.3;
  margin: 6px;
  pointer-events: auto;
  background: white;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .swiper-container-horizontal>.swiper-pagination-bullets & {
    margin: 0 6px; }

  // hover
  &:hover {
    opacity: 0.8; } }

.swiper-pagination-bullet-active {
  opacity: 1;
  cursor: default;

  &:hover {
    opacity: 1;
    cursor: default; } }

/* swiper slider end */
